.hero .container {
  color: #fff;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  margin: 4rem auto;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.hero .left {
  padding: 1rem;

}

.hero h1 {
  padding: 1rem 0;
  
}

.hero right {
  padding: 1rem;
}

.hero .img-container {
  display: flex;
  width: 100%;
}

.hero img {
  max-width: 80%;
  margin: auto;
}

@media screen and (max-width: 940px) {
  .hero .container {
    grid-template-columns: 1fr;
    margin: 1rem 0;
    text-align: center;
  }
}